@import url(https://fonts.googleapis.com/css?family=Cantarell&display=swap);
@import url(https://fonts.googleapis.com/css?family=Hammersmith+One&display=swap);
body {
  margin: 0;
  font-family: "Cantarell", sans-serif;
  background-color: #292929;
  color: white;
}
body #root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.items-counter {
  font-size: 1.1em;
}
.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-end;
  flex-grow: 1;
  margin-top: 1rem;
  width: 70%;
}
.header .logo {
  font-family: "Hammersmith One", sans-serif;
  font-size: 2em;
}
.message {
  margin: 10px auto;
  color: #d49c00;
  font-size: 1.5em;
}
.message i {
  margin-right: 7px;
}
.item-list {
  display: flex;
  justify-content: center;
  font-size: 1.3em;
  border: 1px solid #4b4b4b;
  border-radius: 10px;
  background-color: #252525;
  margin-top: 0.7rem;
  width: 70%;
}
.item-list ul {
  display: flex;
  flex-flow: column wrap;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style: none;
}
.item-list ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #4b4b4b;
}
.item-list ul li:last-child {
  border-bottom: none;
}
.item-list ul li span {
  margin: 10px auto;
}
.item-list ul li .buttons-group {
  padding: 0 10px;
  display: flex;
}
.item-list ul li .buttons-group i {
  padding: 0 10px;
  font-size: 1.3em;
}
.item-list ul li .buttons-group i.done {
  color: #00ad00;
}
.item-list ul li .buttons-group i.delete {
  color: #ff4625;
}
.item-list ul li .buttons-group i.important {
  color: #0082ec;
}
.item-list ul li .buttons-group i:hover {
  cursor: pointer;
}
.item-list ul li .buttons-group i:active {
  -webkit-transform: scale(0.97);
          transform: scale(0.97);
  -webkit-transition: -webkit-transform 0.02s;
  transition: -webkit-transform 0.02s;
  transition: transform 0.02s;
  transition: transform 0.02s, -webkit-transform 0.02s;
}
.item-list ul li .done-item {
  text-decoration: line-through;
  color: #a0a0a0;
}
.item-list ul li .important-item {
  color: #0082ec;
}
.add-form {
  display: flex;
  flex-grow: 1;
}
.add-form input[type=text] {
  background-color: transparent;
  border: 1px solid #4b4b4b;
  width: 100%;
  margin-right: 5px;
  font-size: 1em;
  color: #ffffff;
  padding: 10px 0 10px 20px;
  border-radius: 10px;
  background-color: #2c2c2c;
}
.add-form input[type=text]:focus {
  outline: none;
  background-color: #303030;
}
.add-form .add-button {
  box-shadow: inset 0px 1px 3px 0px #2b2b2b;
  background-color: #2c2c2c;
  border-radius: 5px;
  border: 1px solid #4b4b4b;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 1em;
  font-weight: bold;
  padding: 11px 23px;
  text-decoration: none;
}
.add-form .add-button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  -webkit-transition: -webkit-transform 0.05s;
  transition: -webkit-transform 0.05s;
  transition: transform 0.05s;
  transition: transform 0.05s, -webkit-transform 0.05s;
}
.add-form .add-button:hover {
  background-color: #303030;
}
.add-form .add-button:active {
  background-color: #424242;
}
.add-form .add-button:focus {
  outline: 0;
}
.status-filter {
  display: flex;
  justify-content: flex-end;
}
.status-filter button {
  background-color: #2c2c2c;
  border: 1px solid #4b4b4b;
  cursor: pointer;
  color: #ffffff;
  font-size: 1em;
  font-weight: bold;
  padding: 11px 23px;
  text-decoration: none;
}
.status-filter button:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border-right: none;
}
.status-filter button:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border-left: none;
}
.status-filter button:hover {
  background-color: #303030;
}
.status-filter button:active {
  background-color: #424242;
}
.status-filter button:focus {
  outline: 0;
}
.status-filter button.current {
  background-color: #424242;
}
.app {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  width: 900px;
}
.app .add-elements {
  margin: 0.7rem 0;
  padding: 0;
  display: flex;
  align-items: center;
  width: 70%;
}
.app .status-panel {
  margin-top: 1rem;
  width: 70%;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}
.search-panel {
  width: 50.5%;
}
.search-panel input[type=text] {
  background-color: transparent;
  border: 1px solid #4b4b4b;
  width: 100%;
  margin-right: 5px;
  font-size: 1em;
  color: #ffffff;
  padding: 10px 20px 10px 20px;
  border-radius: 10px;
  background-color: #2c2c2c;
}
.search-panel input[type=text]:focus {
  outline: none;
  background-color: #303030;
}
